<template>
  <div>
    <!-- 设置返回按钮 START-->
    <headerNavBack :centerTitle="centerTitle"></headerNavBack>
    <!-- 设置返回按钮 END -->
    <section>
      <!-- 新需求,暂未开放 -->
      <div class="my_news_item" v-if="0">
        <div class="user_news_ava"><img src="../../../../static/images/j8tryuzf.png" alt="用户头像"></div>
        <div class="user_news_info">
          <div class="user_news_nickname"><span><a href="#">云雀</a></span><span>回复了你</span></div>
          <div><a href="#">你的审美挺棒的!!!我要向你学习~~</a></div>
          <div>2017-03-24</div>
        </div>
        <div class="user_work_info">
          <a><img src="../../../../static/images/da.png" alt="显示作品图片"></a>
        </div>
      </div>
      <!-- 系统通知 -->
      <div class="system_notice_item" v-for="(item, index) in newsList" @click.stop="__enter_news(item)" :key="index">
        <div class="sys_notice">
          <img :src="item && !item.subject ? plane : sysIcon" alt="系统通知">
          <span v-if="item && item.status && item.status == 'UNREAD' && item.sendUserId !== $store.state.user.user.userId"></span>
        </div>
        <div class="sys_notice_info">
          <h4 v-if="item && !item.subject && item.sendUserId == $store.state.user.user.userId">发送的纸条</h4>
          <h4 v-if="item && !item.subject && item.sendUserId != $store.state.user.user.userId">收到的纸条</h4>
          <h4 v-if="item && item.subject">系统通知</h4>
          <p v-if="item && !item.content"><a>{{item.sendUserNickname}}：{{item.noteContent}}</a></p>
          <p v-if="item && item.content"><a>{{item.content}}</a></p>
          <div v-if="item && !item.lastModified"><span>{{item.sendTime | dateFm}}</span><a>快去查看 &gt;</a></div>
          <div v-if="item && item.lastModified"><span>{{item.lastModified | dateFm}}</span><a>快去查看 &gt;</a></div>
        </div>
      </div>
    </section>
    <p class="noWrapper" v-if="!newsList.length"><img :src="noWrapper" alt="没有关注的课程哦~"><br>暂时还没有内容~~宝宝先看看别的~~</p>
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  import { createNamespacedHelpers } from "vuex";
  const { mapActions, mapState } = createNamespacedHelpers(
    "newsModule"
  );
  export default {
    components: {
      headerNavBack,
    },
    data() {
      return {
        // 新闻列表
        newsList: [],
        // 消息图标
        plane: require('../../../assets/img/zhitiao.png'),
        // 设置顶部title
        centerTitle: '我的消息',
        // 系统消息ICON
        sysIcon: require('../../../assets/img/jes0zn6w.png'),
        noWrapper: require('../../../assets/img/da.png')
      }
    },
    methods: {
      ...mapActions([
        'CHANGE_MSG_STATUS',
        'SET_NOTE_STATUS',
        'TIDING_ALL'
      ]),
      // 进入聊天详细页面
      __enter_news(item) {
        this.SET_NOTE_STATUS({
          sendUserId: item.sendUserId,
          noteId: item.noteId,
          status: 'READ'
        })

        if (item.subject) {
          if (item.status != 'READ') {
            this.CHANGE_MSG_STATUS({
              status: item.status,
              sysmsgId: item.messageId
            }).then(data => {
              this.$router.push({
                name: 'infoRe',
                params: {
                  id: item.messageId
                }
              })
            }).catch(err => {
              console.log(err)
            })
          } else {
            this.$router.push({
              name: 'infoRe',
              params: {
                id: item.messageId
              }
            })
          }
        } else {
          // 把当前登录用户ID和发送者ID进行对比,根据发送者来设置 'anotherUserId'
          if (item.sendUserId == this.$store.state.user.user.userId) {
            this.$router.push({
              name: 'newsList',
              params: {
                anotherUserId: item.theRecipient,
                noteId: item.noteId
              }
            })
          } else {
            this.$router.push({
              name: 'newsList',
              params: {
                anotherUserId: item.sendUserId,
                noteId: item.noteId
              }
            })
          }
        }
      },
      // 滑动至底部
      toBottom(time) {
          this.$nextTick(()=>{
            document.scrollingElement.scrollTop = document.scrollingElement.scrollHeight
          })
      },
      // 获取所有消息提示
      getAllNews(){
        this.$Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        })
        this.TIDING_ALL().then((data) => {
          this.$Indicator.close()
          if(Array.isArray(data.data.result.list)){
            this.newsList = data.data.result.list
          }else{
            this.$Toast({
              message: '加载失败,请稍后重试',
              position: 'middle',
              duration: 1000
            })
          }
        }).catch((err) => {
          console.log(err)
        })
      },
    },
    mounted() {
      this.$nextTick(()=>{
        this.getAllNews()
        this.toBottom(200)
      })
    }
  }
</script>


<style lang="less" scoped>
  /* 消息列表 START */
  .my_news_item {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    background-color: #fff;
  }
  .my_news_item>div:nth-child(1) {
    -webkit-box-flex: 2;
    flex: 2;
  }
  .my_news_item>div:nth-child(2) {
    -webkit-box-flex: 6;
    flex: 6;
  }
  .my_news_item>div:nth-child(3) {
    -webkit-box-flex: 2;
    flex: 2;
  }
  .user_news_ava {
    padding: .625rem 0 0 .625rem;
  }
  .user_news_ava>img {
    width: 40px;
    height: 40px;
  }
  .user_news_info {
    padding-top: .625rem;
  }
  .user_news_info>div {
    padding: .1875rem 0;
  }
  .user_news_info>div:nth-child(1)>span:nth-child(2) {
    color: #aaa;
  }
  .user_news_info>div:nth-child(2)>a {
    color: #777;
  }
  .user_news_info>div:nth-child(3) {
    color: #aaa;
  }
  .user_work_info {
    padding-top: .625rem;
  }
  .user_work_info>a {
    display: block;
    width: 40px;
    height: 40px;
  }
  .user_work_info>a>img {
    width: 40px;
    height: 40px;
  }
  /* 系统通知 START */
  .system_notice_item {
    display: -webkit-flex;
    display: flex;
    background-color: #fff;
    padding-bottom: 20px;
  }
  .system_notice_item>div:nth-child(1) {
    -webkit-box-flex: 2;
    flex: 2;
    padding: 30px 0 0 30px;
  }
  .system_notice_item>div:nth-child(1)>img {
    width: 90px;
    height:  90px;
    border-radius: 50%;
    overflow: hidden;
  }
  .system_notice_item>div:nth-child(2) {
    -webkit-box-flex: 8;
    flex: 8;
    padding: 30px 0 0 0;
    margin-left: 20px;
  }
  .sys_notice {
    position: relative;
    &>span {
      content: '';
      display: block;
      width: .625rem;
      height: .625rem;
      background-color: #f00;
      border-radius: 50%;
      position: absolute;
      right: 10%;
      top: 10%;
    }
  }
  .sys_notice_info>p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    &:nth-child(2){
      margin: 10px 0 20px 0;
    }
  }
  .sys_notice_info>h4{
    font-size: 30px;
    font-weight: 400;
    margin-bottom: 10px;
  }
  .sys_notice_info>p>a {
    color: #777;
    font-size: 30px;
  }
  .sys_notice_info>p,
  .sys_notice_info>div {
    padding-right: .625rem;
  }
  .sys_notice_info>div>span {
    float: left;
    color: #aaa;
    font-size: 20px;
  }
  .sys_notice_info>div>a {
    float: right;
    color: #3c8f3d;
  }
  /* 系统通知 END */
  /* 消息列表 END */
  .noWrapper{
    text-align: center;
    &>img{
      width: 300px;
      margin: 40px 0 20px 0;
    }
    color: #aaa;
    font-size: 30px;
  }
</style>



